import { ITimesListaView, ITimesListaController } from "../../contracts/Cadastros/TimesListaContract";
import axios from "../../plugins/axios"

export class TimesListaController implements ITimesListaController {


    _view: ITimesListaView

    constructor(view: ITimesListaView) {
        this._view = view
    }

    importItens(): void {
        this._view.loading(true)

        axios.get('Times/Integracao').then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                //this._view.showMsg("Importação completa.")
                this.carregarItens()
            } else {
                this._view.showMsg(response.data.message)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

    carregarItens(): void {
        this._view.loading(true)

        axios.get('Times').then((response: any) => {
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

}