

















































import Vue from "vue";
import KLoading from "../../../components/KLoading.vue";
import KDialog from "../../../components/KDialog.vue";
import KDataList from "../../../components/KDataList.vue";

import { Display } from "../../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../../models/header";
import { Headers } from "../../../models/headers";

import {
  ITimesListaView,
  ITimesListaController
} from "../../../contracts/Cadastros/TimesListaContract";

import { TimesListaController } from "../../../controllers/Cadastros/TimesListaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class TimesLista extends Vue implements ITimesListaView {
  _controller?: ITimesListaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  dialogMembros = false;

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  fecharItensMembros() {
    this.dialogMembros = false;
  }

  isMobile = false;

  headers = new Headers([
    new Header("Código", "codigo", false, false),
    new Header("Nome", "nome", false, false),
    new Header("Descrição", "descricao", false, false)
  ]);

  headersMembros = new Headers([
    new Header("Colaborador", "colaborador", false, false),
    new Header("Status", "status", false, false)
  ]);

  itens = [];
  itensMembros = [];

  showMembros(item: any) {
    this.itensMembros = item.membros;
    this.dialogMembros = true;
  }

  importLista() {
    this._controller!.importItens();
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new TimesListaController(this);

    this._controller.carregarItens();
  }
}
